.tb_l_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
}

.tb_l_post_wrapper{
    width: 100%;
}

.tb_l_post_in{
    display: block;
    position: relative;
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
    padding: 15px;
}

.tb_l_media_list {
    display: flex;
    width: 100%;
    align-items: center;
    flex-wrap: wrap;
    justify-content: start;
    margin-bottom: 15px;
}

.tb_l_media_cont{
    display: flex;
    align-items: center;
    justify-content: star;
    flex-wrap: wrap;
}

.tb_l_media_wrap{
    width: 100%;
    max-width: 80px;
    position: relative;
    margin-right: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.tb_l_post_media_wrapp{
    position: relative;
}

.tb_l_media_icon .tb_center_video_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    z-index: 2;
}

.tb_l_media_icon .tb_center_video_ico{
    color: #fff;
    opacity: .8;
}

.tb_l_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}
.tb_l_image, .tb_l_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_l_video{
    background-color: #000;
}

.tb_l_video video,
.tb_l_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_l_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_l_share_container{
    z-index: 4;
}

.tb_l_author_wrapper {
    margin-bottom: 10px;
}


.tb_l_author_profile {
    height: 40px;
    width: 40px;
    border-radius: 4px;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
}
.tb_l_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_l_authorname {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 4px;
    text-align: left;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_l_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_l_author_wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tb_l_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_l_post_info {
    display: flex;
    align-items: center;
}

.tb_l_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_l_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_l_content,
.tb_l_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_l_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
    max-width: 100%;
}
.tb_l_rating__{
    margin: 10px 0;
}

.tb_l_content {
    margin-bottom: 15px;
    white-space: pre-line;
    word-break: break-word;
}
.tb_l_rating_content{
    margin-bottom: 15px;
}
.tb_l_rating_content, .tb_l_text_post {
    text-align: center;
}

.tb_l_p_wrp{
    display: flex;
    align-items: center;
    padding: 8px;
    background-color: rgba(226, 226, 226, 0.30);
    max-width: 270px;
    width: 100%;
    border-radius: 4px;
    margin-right: 5px;
    margin-bottom: 5px;
}

.tb_l_p_{
    width: 60px;
    height: 60px;
    margin-right: 15px;
    border-radius: 4px;
    overflow: hidden;
}

.tb_l_p_img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.tb_l_p_t{
    font-size: 14px;
    margin-bottom: 5px;
    
}

.tb_l_p_list{
    display: flex;
    flex-wrap: wrap;
}
